import React, {memo, useRef, useState} from 'react';
import WebView from 'react-native-webview';
import {interactManager} from '@true/core';
import {ActivityIndicator, View} from 'react-native';

const jsCode = `(function() {
  window.ReactNativeWebView.postMessage(JSON.stringify(document.getElementsByTagName("title")));
})();`;

export const FileWebView = memo(({url}: {url: string}) => {
  const ref = useRef<WebView>(null);
  const timeout = useRef<any>(null);
  const timeout2 = useRef<any>(null);

  const [mounted, setMounted] = useState(false);

  return (
    <View style={{flex: 1}}>
      <WebView
        ref={ref}
        style={{opacity: 99, flex: 1}}
        scalesPageToFit={false}
        javaScriptEnabled={true}
        injectedJavaScript={jsCode}
        onMessage={e => {
          if (e.nativeEvent?.data == '{}') {
            return interactManager(
              () => {
                ref.current?.reload();
              },
              300,
              true,
              timeout,
            );
          }
          if (!mounted) {
            interactManager(
              () => {
                setMounted(true);
              },
              300,
              true,
              timeout2,
            );
          }
        }}
        // injectedJavaScript={`const meta = document.createElement('meta'); meta.setAttribute('content', 'width=device-width, initial-scale=1, maximum-scale=1 ,user-scalable=1'); meta.setAttribute('name', 'viewport'); document.getElementsByTagName('head')[0].appendChild(meta); `}
        source={{
          // uri: `https://view.officeapps.live.com/op/embed.aspx?src=${url}`,
          uri: `http://docs.google.com/gview?url=${url}&embedded=true`,
        }}
      />
      {!mounted ? (
        <View
          style={{
            position: 'absolute',
            bottom: 0,
            top: 0,
            width: '100%',
            alignItems: 'center',
            justifyContent: 'center',
          }}>
          <ActivityIndicator />
        </View>
      ) : null}
    </View>
  );
});
